.home.posts .summary {

    backdrop-filter: blur(2px);
    border-radius: 10px;
    box-shadow: 6px 6px 6px rgba(20, 134, 211, 0.4);
    margin: 18px 0;
    pointer-events: auto;
    padding: 4px ;
  
    &::before,
    &::after {
      content: "";
      position: absolute;
      width: 0;
      height: 0;
      transition: .6s ease-in-out; /* è¿‡æ¸¡åŠ¨ç”»è®¾ç½® */
      border-color: transparent;
    }
  
    &::before {
      top: 0;
      left: 0;
      border-top: 0 solid #1890ff;
      border-left: 0 solid #1890ff;
      
    }
  
    &::after {
      right: 0;
      bottom: 0;
      border-bottom: 0 solid #1890ff;
      border-right: 0 solid #1890ff;
    }
  
    &:hover::before,
    &:hover::after {
      pointer-events: none;
      border-width: 2px; /* é¼ æ ‡æ‚¬åœæ—¶è¾¹æ¡†å®½åº¦ */
      width: 100%; /* åŠ¨ç”»ä»Ž0æ‰©å±•åˆ°æ•´ä¸ªå…ƒç´  */
      height: 100%; /* åŒä¸Šï¼Œè®¾ç½®åŠ¨ç”»æ‰©å±•é«˜åº¦ */
    }

}

.home.posts .summary:not(:last-of-type)::after {
  /* åˆ†å‰²çº¿ */
  display: none;
}


