.container:has(.home) {
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    width: 100%;
    height: MAX(100vh, 400px);
    background-image: linear-gradient(90deg, rgba(60, 10, 30, .04) 1px, transparent 0), linear-gradient(1turn, rgba(60, 10, 30, .04) 1px, transparent 0);
    background-size: 40px 40px;
    background-position: center;
    border-bottom: 1px solid rgba(60, 10, 30, .04);

    [data-theme='dark'] & {
      background-image: linear-gradient(90deg, rgba(195, 245, 215, .04) 1px, transparent 0), linear-gradient(1turn, rgba(195, 245, 215, .04) 1px, transparent 0);
      border-bottom-color: rgba(195, 245, 215, .04);
    }
  }
}



#header-mobile {
  .header-wrapper {
    max-height: 3.5rem;
  }

  .header-subtitle {
    font-size: 0.55em;
  }
}

.single {
  .single-subtitle {
    color: #57606a;
  }

  .content {
    background-image: linear-gradient(90deg, rgba(60, 10, 30, .04) 3%, transparent 0), linear-gradient(1turn, rgba(60, 10, 30, .04) 3%, transparent 0);
    background-size: 20px 20px;
    background-position: center;

    [data-theme='dark'] & {
      background-image: linear-gradient(90deg, rgba(195, 245, 215, .04) 3%, transparent 0), linear-gradient(1turn, rgba(195, 245, 215, .04) 3%, transparent 0);
    }
  }

  // Content adapter generated posts style
  &[data-adapters='projects'] {
    .single-subtitle {
      font-size: 1rem;
    }
  }
}

.home {

  img {
    mask-image: linear-gradient(180deg,
        #fff 0%,
        rgba(255, 255, 255, 0.8) 65%,
        rgba(255, 255, 255, 0.4) 100%);

    [data-theme='dark'] & {
      mask-image: linear-gradient(180deg,
          #000 0%,
          rgba(0, 0, 0, 0.8) 65%,
          rgba(0, 0, 0, 0.4) 100%);
    }
  }

  &.posts {
    .home-profile {
      box-sizing: border-box;
      height: MAX(calc(100vh - 3.5rem), 400px);
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }

    .home-avatar {
      /** 
        https://raw.githubusercontent.com/geekswg/geekswg/snake/snake.svg æºåœ°å€ 
        ä½¿ç”¨å›¾åºŠåŽ https://picx.xpoet.cn/
        https://cdn.jsdelivr.net/gh/geekswg/geekswg@snake/snake.svg
      */
      /** è´ªåƒè›‡åŠ¨ç”»èƒŒæ™¯ */
      background-image: url(https://testingcf.jsdelivr.net/gh/geekswg/geekswg@snake/github-contribution-grid-snake.svg);
      -moz-background-size: 100% 100%;
      background-size: 100% 100%;
